header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0 0 0;
  margin-bottom: 50px;
}
header nav {
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 20px;
}
header li {
  list-style: none; 
  font-size: 1.3rem;
}
header h1 {
  margin: 0;
}
.active {
  color:#c7daca;
  text-decoration: underline;
}