.itemDetails-container {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	margin-bottom: 20px;
	background: #303030;;
}
.itemDetails-container img {
	max-width: 250px;
	margin-right: 50px;
}
.itemDetails-container h3 {
	margin-bottom: 30px;
}
.itemDetails-container p:not(:first-child) {
	border-top: 1px solid rgb(132 132 132);
	margin: 0;
	padding: 10px;
}
.itemContent-container span {
	margin-left: 10px;
	font-size: 1.1rem;
	font-weight: 700;
}
