.randomPlanet-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  column-gap: 40px;
  background: #303030;   
  margin-bottom: 50px;
  max-height: 300px;
}
.randomPlanet-container img { 
  max-width: 30%;
  max-height: 300px;
}
.randomPlanet-container h3 {
  margin-bottom: 20px;
 }
.randomPlanet-container p {
 border-top: 1px solid rgb(132, 132, 132);
 margin: 0;
 padding: 10px;
}
.randomPlanet-container span {
 font-weight: 700;
 margin-left: 20px;
 font-size: 1.1rem;
}